	module.exports = {"data":{"markdownRemark":{"html":"<h1>Heading 1</h1>\n<h2>Heading 2</h2>\n<h3>Heading 3</h3>\n<h4>Heading 4</h4>\n<h5>Heading 5</h5>\n<h6>Heading 6</h6>\n<h1>Markdown Review</h1>\n<p>It's very easy to make some words <strong>bold</strong> and other words <em>italic</em> with Markdown. You can even <a href=\"http://google.com\">link to Google!</a></p>\n<ol>\n<li>One</li>\n<li>Two</li>\n<li>Three</li>\n</ol>\n<p>or:</p>\n<ul>\n<li>Start a line with a star</li>\n<li>Profit!</li>\n</ul>\n<p>or:</p>\n<ul>\n<li>Dashes work just as well</li>\n<li>\n<p>And if you have sub points, put two spaces before the dash or star:</p>\n<ul>\n<li>Like this</li>\n<li>And this</li>\n</ul>\n</li>\n</ul>\n<p>If you want to embed images, this is how you do it:</p>\n<p><img src=\"https://octodex.github.com/images/yaktocat.png\" alt=\"Image of Yaktocat\">\n\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 590px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 56.25%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"flowers from media folder\"\n        title=\"\"\n        src=\"/static/flowers-9af3809dc74c8d238e565fa63ef585bd-aaba4.jpg\"\n        srcset=\"/static/flowers-9af3809dc74c8d238e565fa63ef585bd-6d4e0.jpg 148w,\n/static/flowers-9af3809dc74c8d238e565fa63ef585bd-6a66a.jpg 295w,\n/static/flowers-9af3809dc74c8d238e565fa63ef585bd-aaba4.jpg 590w,\n/static/flowers-9af3809dc74c8d238e565fa63ef585bd-51f5d.jpg 885w,\n/static/flowers-9af3809dc74c8d238e565fa63ef585bd-ec096.jpg 1180w,\n/static/flowers-9af3809dc74c8d238e565fa63ef585bd-730a0.jpg 1770w,\n/static/flowers-9af3809dc74c8d238e565fa63ef585bd-fb9fa.jpg 3264w\"\n        sizes=\"(max-width: 590px) 100vw, 590px\"\n      />\n    </span>\n  </span>\n  </p>\n<p>If you'd like to quote someone, use the > character before the line:</p>\n<blockquote>\n<p>Coffee. The finest organic suspension ever devised... I beat the Borg with it.</p>\n<ul>\n<li>Captain Janeway</li>\n</ul>\n</blockquote>\n<p>There are many different ways to style code with GitHub's markdown. If you have inline code blocks, wrap them in backticks: <code class=\"language-text\">var example = true</code>.  If you've got a longer block of code, you can indent with four spaces:</p>\n<div class=\"gatsby-highlight\" data-language=\"text\">\n      <pre class=\"language-text\"><code class=\"language-text\">if (isAwesome){\n  return true\n}</code></pre>\n      </div>\n<p>GitHub also supports something called code fencing, which allows for multiple lines without indentation:</p>\n<div class=\"gatsby-highlight\" data-language=\"text\">\n      <pre class=\"language-text\"><code class=\"language-text\">if (isAwesome){\n  return true\n}</code></pre>\n      </div>\n<p>And if you'd like to use syntax highlighting, include the language:</p>\n<div class=\"gatsby-highlight\" data-language=\"language-js\">\n      <pre class=\"language-language-js\"><code class=\"language-language-js\">if (isAwesome){\n  return true\n}</code></pre>\n      </div>\n<h2>Extras</h2>\n<ul>\n<li class=\"task-list-item\"><input type=\"checkbox\" checked disabled> This is a complete item</li>\n<li class=\"task-list-item\"><input type=\"checkbox\" disabled> This is an incomplete item</li>\n</ul>","frontmatter":{"title":"Template Project Post"}}},"pathContext":{"slug":"/pages/projects/ze-template-post/post/"}}

